import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'page-trip-details',
  templateUrl: 'trip-details.html',
})
export class TripDetailsPage {

  trip_id: any = "Chargement...";
  response: Observable<any>;
  trip_details: Observable<any>;
  id: any;
  token: any;
  stops: any = [];
  hours: any = [];
  realTime: any = [];
  seconds: number;

  constructor(public httpClient: HttpClient, public navCtrl: NavController, public navParams: NavParams) {
    this.trip_id = navParams.get('id');
    this.id = navParams.get('userid');
    this.token = navParams.get('user_token');
    this.loadData();
  }

  loadData() {
    this.response = this.httpClient.get('https://voisibus.fr/api/get/trip_details.php?trip=' + this.trip_id + '&id=' + this.id + '&token=' + this.token);
    this.response
    .subscribe(data => {
      this.trip_details = data;
      this.stops = JSON.parse(JSON.stringify(this.trip_details))[0].stops.split(';');
      this.hours = JSON.parse(JSON.stringify(this.trip_details))[0].intervals.split(';');
      var time = JSON.parse(JSON.stringify(this.trip_details))[0].hour.split(':');
      this.seconds = (+time[0]) * 60 * 60 + (+time[1]) * 60;
      for(var i = 0; i < this.hours.length; i++) {
        this.seconds = Number(this.seconds) + Number(this.hours[i]);
        this.realTime.push(this.secondsToHms(this.seconds));
      } 
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TripDetailsPage');
  }

  secondsToHms(d: any) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor(d % 3600 / 60);
    //var s = Math.floor(d % 3600 % 60);

    var hDisplay = h + ":";
    var mDisplay = m == 0 ? "00" : m;
    //var sDisplay = s;
    return hDisplay + mDisplay; 
  }

}



